.App {
  text-align: center;
  background-color: #FFBA73;
  font-family: 'Play', sans-serif;
  font-family: 'Chakra Petch', sans-serif;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.calendar{
  display: grid;
  height: 80vh;
  margin: 0 auto;
  grid-template-columns:repeat(7, 100px);
  grid-template-rows:auto;
  background: #FF8100;
  grid-gap: 2px;
  justify-content: center;
  width: 700px;
}
.calendar__day{
  background: #FFA040;
  padding: 10px;
  transition: all .2s;
}
.calendar__day:hover{
  background-color: #FF8100;
}
.modal{
  position: absolute;
  top:200px;
  left:25%;
  width: 400px;
  height: 200px;
  background-color: burlywood;
}
.btn{
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  padding: 20px;
  margin: 20px;
  background-color: #BF7830;
  color: #FFBA73;
}